html {
  box-sizing: border-box;
  height: 100%; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  padding: 0;
  margin: 0;
  width: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  line-height: 22px;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto;
  font-size: 0;
  line-height: 0; }

svg,
picture {
  font-size: 0;
  line-height: 0; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }
  .visually-hidden:not(:focus):not(:active) {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip: rect(0 0 0 0);
    overflow: hidden; }

.btn {
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  display: block;
  background-color: #0ad9c6; }

.btn__submit {
  color: #000000;
  font-weight: 600;
  font-size: 1.03vw;
  line-height: 21px;
  text-align: center;
  border-radius: 6px;
  padding: 16px 20px 14px; }
  @media (max-width: 1023px) {
    .btn__submit {
      font-size: 15px;
      margin-right: 0; } }
  @media (max-width: 767px) {
    .btn__submit {
      padding: 14px 20px 16px; } }

.btn__menu {
  display: none; }
  @media (max-width: 1023px) {
    .btn__menu {
      position: absolute;
      top: -25px;
      right: -22px;
      display: block;
      width: 50px;
